@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"

html
   box-sizing: border-box

*,
*::before,
*::after
   box-sizing: border-box
    // min-height: 0.001vw // — Fixes Safari bug with viewport units in calc()

.content-box-component
   box-sizing: content-box

body
   display: flex
   flex-direction: column
   background: #fff
   font-family: 'Montserrat', 'Helvetica', Arial, sans-serif
   width: 100%
   height: 100%
   min-height: 100vh
   -webkit-font-smoothing: antialiased
   -moz-osx-font-smoothing: grayscale
   color: var(--primary)
   font-size: 16px

a
   text-decoration: none
   color: inherit

button
   padding: 0
   cursor: pointer
   background: transparent
   border: none
   color: inherit

ul, ol
   margin: 0
   padding: 0

ul
   list-style: none

p
   margin: 0

h1,
h2,
h3,
h4,
h5,
h6
   margin: 0

img
   display: block
   max-width: 100%
   height: auto

picture
   display: block

.visually-hidden:not(:focus):not(:active)
   position: absolute
   width: 1px
   height: 1px
   margin: -1px
   border: 0
   padding: 0
   clip: rect(0 0 0 0)
   overflow: hidden

.background-image
   position: absolute
   top: 0
   bottom: 0
   left: 0
   right: 0
   overflow: hidden
   picture
      position: absolute
      top: 0
      bottom: 0
      left: 0
      right: 0
   img
      width: 100%
      height: 100%
      object-fit: cover
      object-position: center

.wrapper
   width: 100%
   max-width: 1440px
   min-width: 320px
   padding: 20px
   margin: 0 auto

@media screen and (max-width: $width_xs)
   .wrapper
      padding: 16px